
.contenedor-dashboard{
    background: #E7E4E3;

}

.row-md{
    display: grid;
    grid-template-columns: 50% 50%;   
    background: white;

}

.col-md{
    width: 99%;
    border: 2px solid #E7E4E3;


}
.col-md-titulo{
    width: 100%;
    border: 1px solid #E7E4E3;
    padding-top: 1%;
    padding-bottom: 2%;

}
.text-titulo-md{
    padding-left: 5%;
    padding-top: 1%;
    color: #484746;
    font-family: 'Lato',sans-serif, Arial;
    font-weight: bold;
}

.row-sm{	
    display: grid;
    grid-template-columns: 50% 50%;
    background: white;
}
.col-sm{
    width: 99%;
    border: 4px solid #E7E4E3;
    text-align: center;

}
.container-col-sm{
    display: grid;
    grid-template-columns: 50% 50%;
}

.canvas-donas{
    /*    width: 500px !important;
        height: 500px !important;*/
}

.titulo-sm{
    height: 30%;
    padding: 2%;
    border: 2px solid #E7E4E3;
}
.cuerpo-sm{
    font-size: 170%;
    padding: 3%;
    border: 2px solid #E7E4E3;
}
